import React, { Component } from 'react';
import './App.css';
import BookView from './Components/BookView';
import { RenderMode } from './constants';
import Controls from './Components/Controls';
import componentMapping from './component-mappings';
import { PAGE_BREAK } from './constants';
import Sidebar from './Components/Sidebar';
import { contents } from './og1_search.json';

class App extends Component {
  state = {
    renderMode: RenderMode.Web,
    currentPage: 1,
    searchResults: [],
    allowPageSwitch: true,
    nativeSize: false
  }

  componentDidMount = () => {
    this.setState({
      currentPage: parseInt(window.location.hash.substring(1)) || 1
    });

    this.onWindowResize();
    window.addEventListener("resize", this.onWindowResize)

    window.addEventListener(
      "hashchange",
      () => this.goToPage(parseInt(window.location.hash.substring(1)) || this.state.currentPage)
    );

    window.addEventListener("keydown", (e) => {
      if (e.keyCode === 37) this.onPrev();
      else if (e.keyCode === 39) this.onNext();
    }, false);
  }

  onWindowResize = () => {
    this.setState({
      renderMode: window.innerWidth < PAGE_BREAK ? RenderMode.Mobile : RenderMode.Web
    });
  }

  onNext = () => {
    this.goToPage(
      this.state.currentPage + this.numPageToFlip()
    );
  }
  onPrev = () => {
    this.goToPage(
      this.state.currentPage - this.numPageToFlip()
    );
  }

  numPageToFlip = () => this.state.renderMode === RenderMode.Mobile ? 1 : 2

  goToPage = (pageNum) => {
    const page = Math.min(Math.max(1, pageNum), componentMapping.length - 2)
    window.location.hash = page;
    this.setState({
      currentPage: page
    }, () => window.scrollTo(0, 0))
  }

  doSearch = (value) => {
    if (!value) this.setState({ searchResults: [] });
    else {
      const values = value.toLowerCase().split(" ").map(item => item.trim()).filter(item => !!item);
      var results = contents
        .map((c, index) => ({ text: c, pageNum: index }))
        .filter(c => {
          for (let val of values) {
            if (c.text.toLowerCase().indexOf(val) > -1) {
              return true;
            }
          }
          return false;
        });
      this.setState({
        searchResults: results
      });
    }
  }

  toggleNativeSize = () => this.setState({ nativeSize: !this.state.nativeSize })

  render = () => (
    <div>
      <Sidebar doSearch={this.doSearch} searchResults={this.state.searchResults} goToPage={this.goToPage} />
      <BookView renderMode={this.state.renderMode} currentPage={this.state.currentPage} nativeSize={this.state.nativeSize} />
      <Controls
        onNext={this.onNext}
        onPrev={this.onPrev}
        currentPage={this.state.currentPage}
        haveNext={this.state.currentPage < (componentMapping.length - 2)}
        havePrev={this.state.currentPage > 1}
        goToPage={this.goToPage}
        percent={100 * this.state.currentPage / (componentMapping.length - 2)}
        nativeSize={this.state.nativeSize}
        toggleNativeSize={this.toggleNativeSize}
      />
    </div>
  )
}

export default App;
