import React, { PureComponent } from 'react';

class Controls extends PureComponent {
  state = {
    hovered: false,
    currentPage: 1
  };

  onMouseEnter = () => {
    if (!this.state.hovered) this.setState({ hovered: true })
  }

  renderSizeButton = () => this.props.nativeSize
    ? (<button className="controls_button" onClick={this.props.toggleNativeSize}><i className="icon-fullscreen-exit" /></button>)
    : (<button className="controls_button" onClick={this.props.toggleNativeSize}><i className="icon-fullscreen" /></button>)

  render() {
    return (
      <div className={`control_panel ${this.state.hovered ? "" : "active"}`} onMouseEnter={this.onMouseEnter}>
        <div className="controls">
          {this.renderSizeButton()}
          <button className="controls_button" id="prev" disabled={!this.props.havePrev} onClick={this.props.onPrev}><i className="icon-arrow-left" /></button>
          <button className="controls_button" id="next" disabled={!this.props.haveNext} onClick={this.props.onNext}><i className="icon-arrow-right" /></button>
          <div className="progress_bar">
            <div className="progress_fill" style={{ width: this.props.percent + "%" }}></div>
          </div>
          <span style={{ color: '#ffffff', fontWeight: 'bold' }}>{this.props.currentPage}</span>
        </div>
      </div>
    );
  }
}

export default Controls;