import React, { PureComponent } from 'react';

class Sidebar extends PureComponent {
  state = {
    isActive: false,
    search: ""
  };

  onClick = () => {
    this.setState({
      isActive: !this.state.isActive
    });
  }

  onSearchChange = (e) => {
    this.setState({
      search: e.target.value.trim()
    }, () => {
      if (!this.state.search) this.props.doSearch(this.state.search);
    });
  }

  clear = () => this.setState({ search: "" })

  onSubmit = (e) => {
    e.preventDefault();
    this.props.doSearch(this.state.search);
  }

  onClickItem = (pageNum) => this.props.goToPage(pageNum);

  renderSearchResult = () => this.props.searchResults.map(item => (<div key={item.pageNum} className="result_item" onClick={() => this.onClickItem(item.pageNum)}>
    <h5>Page {item.pageNum}</h5>
    <div className="page_preview">{item.text}</div>
  </div>))

  render() {
    return (
      <div className={`side_bar ${this.state.isActive ? 'active' : ''}`}>
        <button className="controls_button float-right" onClick={this.onClick}><i className="icon-menu" /></button>
        <form className="search" onSubmit={this.onSubmit}>
          <div className="input-group">
            <input type="text" className="form-control" placeholder="Search" name="search" onChange={this.onSearchChange} />
            <div className="input-group-append">
              <button className="btn btn-secondary" type="submit" id="button-addon2"><i className="icon-search" /></button>
            </div>
          </div>
          <div className="search_result">{this.renderSearchResult()}</div>
        </form>
      </div>
    );
  }
}

export default Sidebar;